import { FC } from 'react';
import { clsx } from 'clsx';

import { LinkProps, LinkSize, LinkTarget, LinkMode } from './link.interface';

import { FunctionalIcon } from '../icon/Icon';

export const Link: FC<LinkProps> = ({
  as: Tag = 'a',
  size = LinkSize.Base,
  mode = LinkMode.Inline,
  disabled = false,
  target = LinkTarget.Self,
  label,
  ...props
}) => {
  const fontSize = size === LinkSize.Base ? '' : 'zep-text-0.875';
  const disabledText = disabled ? 'group-hover:zep-no-underline' : 'group-hover:zep-underline';

  return (
    <Tag
      className={clsx(
        fontSize,
        'zep-outline-none',
        { 'zep-ring-focus focus-visible:zep-ring': Tag === 'a' },
        'zep-border-4',
        'zep-border-separate',
        'zep-leading-1.25',
        'zep-inline-flex',
        'zep-flex-row',
        'zep-flex-nowrap',
        'zep-items-center',
        'group',
        'group-active:zep-underline group-active:zep-decoration-2',
        'zep-cursor-pointer',
        'zep-underline-offset-2',
        { 'zep-underline zep-decoration-1 hover:zep-decoration-2': mode === LinkMode.Inline },
        { 'zep-cursor-not-allowed zep-opacity-disabled': disabled },
      )}
      tabIndex={disabled ? -1 : 0}
      aria-disabled={disabled}
      target={target}
      {...props}
    >
      <span className={clsx(disabledText)}>{label}</span>
      {mode === LinkMode.Standalone && (
        <FunctionalIcon name="arrow-long-right" className={clsx({ 'zep-ml-0.25': label })} />
      )}
    </Tag>
  );
};
